import { useState } from 'react'
import { useMediaQuery } from 'react-responsive'
import { NavLink } from 'react-router-dom'
import { data } from '../../Data/data'
import { MenuContainer, ListItem, ListContainer, IconMenu, StyledLink} from './MenuStyles'
import { MenuResponsive } from './MenuResponsive'

const Menu = () => {

    const isTabletOrMobile = useMediaQuery({ query: '(max-width: 768px)'})


  return (
    <>
        {isTabletOrMobile ? (
            <MenuResponsive />
        ) : (
            <MenuContainer>
            <ul>
            {data.map((item) => (
                <ListItem>  
                    <NavLink to={item.path}>
                        <IconMenu></IconMenu>
                    </NavLink>
                    
                    <StyledLink active to={item.path} >
                        {item.name}
                    </StyledLink>
                    
                </ListItem>
                )
            )}
            <li>
            <ListItem>  
                    <a href="https://equipo-desarrollo.gitbook.io/whitepaper/whitepaper/" target={"_blank"}>
                        <IconMenu></IconMenu>
                    </a>
                    
                    <StyledLink rel="noopener noreferrer" to="https://equipo-desarrollo.gitbook.io/whitepaper/whitepaper/" target={"_blank"} >
                        WHITEPAPER
                    </StyledLink>
                    
                </ListItem>
        </li>
        </ul>
        </MenuContainer>
        )}
    </>
  )
}

export default Menu